* {
	box-sizing: border-box;
}

body {
	background-color: #9ed8db;
	margin: 0;
}

h1 {
	color: white;
	font-family: 'Fugaz One', cursive;
	font-size: 50px;
}

input,
label,
p,
textarea {
	color: #1d3354;
	border: none;
	border-radius: 0;
	font-family: 'Open Sans', sans-serif;
	font-size: 18px;
}

input {
	outline: none;
}

input[type='email'],
input[type='number'],
input[type='text'] {
	background: none;
	border-bottom: 2px solid #1d3354;
	padding: 5px;
}

input[type='email']:focus,
input[type='number']:focus,
input[type='text']:focus {
	border-color: #d64045;
}

label {
	display: inline-block;
	margin-bottom: 5px;
}

textarea {
	border: 2px solid #1d3354;
	height: 100px;
	outline: none;
	width: 100%;
}

textarea:focus {
	border-color: #d64045;
}

#content {
	background-color: #e9fff9;
	border-radius: 50px;
	margin: 50px auto;
	max-width: 600px;
	width: 90vw;
}

#description {
	margin: 20px;
}

#note {
	font-size: 12px;
	margin: 0 20px;
}

#particles-js {
	height: 100vmax;
	left: 0;
	position: fixed;
	top: 0;
	width: 100vmax;
	z-index: -1;
}

#submit {
	background-color: #467599;
	border: none;
	border-radius: 10px;
	width: 120px;
}

#submit p {
	color: white;
}

#survey-form {
	padding: 5vh 5vw;
}

#title-container {
	align-items: center;
	background-color: #1d3354;
	border-top-left-radius: 50px;
	border-top-right-radius: 50px;
	box-shadow: 0 5px 5px #467599;
	display: flex;
	height: 80px;
	justify-content: center;
	text-align: center;
	width: 100%;
}

@media only screen and (max-width: 700px) {
	h1 {
		font-size: 30px;
	}
}
